<template>
  <div class="main">
    <div class="topTool">
      <el-form :inline="true" :model="formInline" class="demo-form-inline" size="small">
        <el-form-item label="角色">
          <el-input v-model="formInline.roleName" placeholder="角色"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="formInline.description" placeholder="描述"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="selectRole">搜索</el-button>
          <el-button type="primary" icon="el-icon-edit" @click="updateRole">编辑</el-button>
          <el-button type="primary" icon="el-icon-share" @click="addRole">新增</el-button>
          <el-button type="primary" icon="el-icon-delete" @click="deleteRole">删除</el-button>
          <el-button type="primary">
            上传
            <i class="el-icon-upload el-icon--right"></i>
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="flex-table">
      <div class="flex-table-wrap" style>
        <el-table
          v-loading="loading"
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%;height:100%;"
          border
          size="small"
          @selection-change="changeFun"
        >
          <el-table-column label="多选" type="selection" width="40px"></el-table-column>
          <el-table-column prop="roleId" label="ID" width="80px"></el-table-column>
          <el-table-column prop="roleName" label="姓名" width="150px"></el-table-column>
          <el-table-column prop="description" label="描述" width="150px"></el-table-column>
          <el-table-column prop="creationTime" label="创建时间" width="200px" show-overflow-tooltip></el-table-column>
        </el-table>
      </div>
    </div>
    <div class="bottomPagination">
      <div class="xd-pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          small
        ></el-pagination>
      </div>
    </div>
    <div v-if="isadd===1">
      <el-dialog title="新增角色" :visible.sync="dialogFormVisible" width="400px">
        <el-form :model="addForm" label-width="80px">
          <el-form-item label="角色">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="描述">
            <el-input v-model="addForm.description"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addRoleSubmit">添加</el-button>
            <el-button @click="cancel()">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
    <div v-if="isadd===2">
      <el-dialog title="编辑角色" :visible.sync="dialogFormVisible">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="角色">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="描述">
            <el-input v-model="addForm.description"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateRoleSubmit">提交</el-button>
            <el-button @click="cancel()">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  name: "SysExceptionLogList",
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      tableData3: [],
      loading: true,
      formInline: {
        roleName: "",
        description: ""
      },
      dialogFormVisible: false,
      isadd: 1,
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      addForm: {
        roleId:"",
        roleName: "",
        description: ""
      },
      multipleSelection: []
    };
  },
  computed: {},
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this.selectRole();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.selectRole();
    },
    selectRole() {
      this.$ajax({
        method: "post",
        url: "selectRole",
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          roleName: this.formInline.roleName,
          description: this.formInline.description
        }
      })
        .then(res => {
          this.tableData3 = res.data.data;
          this.total = res.data.total;
          this.loading = false;
        })
        .catch(err => {
          alert(err);
        });
    },
    updateRole() {
      if (this.multipleSelection == 0) {
        alert("请选择需要编辑的行");
      } else if (this.multipleSelection.length >= 2) {
        alert("不能编辑多行");
      } else {
        (this.isadd = 2), (this.dialogFormVisible = true);
        this.addForm.roleId = this.multipleSelection[0].roleId;
        this.addForm.roleName = this.multipleSelection[0].roleName;
        this.addForm.description = this.multipleSelection[0].description;
      }
    },
    updateRoleSubmit() {
      this.$ajax({
        method: "post",
        url: "updateRole",
        params: {
          roleId:this.addForm.roleId,
          roleName: this.addForm.roleName,
          description: this.addForm.description
        }
      })
        .then(res => {
          this.dialogFormVisible = false;
          this.selectRole();
          this.claerAddForm();
        })
        .catch(err => {
          alert(err);
        });
    },
    addRole() {
      (this.isadd = 1), (this.dialogFormVisible = true);
    },
    addRoleSubmit() {
      this.$ajax({
        method: "post",
        url: "addRole",
        params: {
          roleName: this.addForm.roleName,
          description: this.addForm.description
        }
      })
        .then(res => {
          this.dialogFormVisible = false;
          this.selectRole();
          this.claerAddForm();
        })
        .catch(err => {
          alert(err);
        });
    },
    deleteRole() {
        this.$ajax({
        method: "post",
        url: "deleteRole",
        data: this.multipleSelection
      })
        .then(res => {
          alert(res.data)
          this.selectRole()
        })
        .catch(err => {
          alert(err);
        });
    },
    deleteRoleSubmit(){

    },
    changeFun(val) {
      this.multipleSelection = val;
    },
    claerAddForm(){
      this.addForm.roleId = ""
      this.addForm.roleName = ""
      this.addForm.description = ""
    },
    cancel(){
          this.dialogFormVisible = false;
          this.claerAddForm();
    }
  },

  mounted() {
    this.selectRole();
  }
};
</script>

